
.messageContainer {

    color: #61dafb;
    width:400px;
    height: 100%;
    display: flex;

    align-items: end  ;
    margin-left:20px ;
    margin-bottom: 30px;


}
.messageBlock {
width: 29px;
    height: 28px;

    position: relative;
}
.messageBlockCircle{
    width: 25px;
    height: 30px;
    border-radius:13px;
    background-color: #fff;
    position: absolute;
    top: 0px;
    z-index: 1;
    left: 1px;
}
.messageBlockBox{
    background-color: #ffe0e9;
    width: 15px;
    height: 15px;
    position: absolute;
    bottom: 0;
    right: 0    ;
}
.photo{
    width: 60px;
    height: 60px;
    border-radius: 50px;
    border: 1px solid rgba(40, 44, 52, 0);

}

.message{
    background-color: #ffe0e9;
    border-radius: 5px 5px 5px 0px;
    padding: 10px;
    width: 300px;

}
.textContainer{
    display: flex;
    justify-content: space-between;
    margin: 0;
    align-items: end;
}
.name{
    margin: 0;
    color: #353741;
}
.text{
    color: #6c6d72;
    margin: 0;
    width: 250px;
    text-align: justify;



}
.time{
    margin: 0;
    color: rgba(44, 32, 34, 0.46);
    margin: 2px;
}